import {
  Component, ElementRef, Input, Output, AfterViewInit, TemplateRef, ViewChild,
  EventEmitter, OnInit, AfterContentInit, OnChanges, SimpleChanges, OnDestroy
} from '@angular/core';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularCustomEventType,
  AdaptRadarAngularService
} from '../common/radar/adapt-radar';

import {
  AdaptNavigationSubMenuItem,
  AdaptSwitcherModel,
  NavigationAction,
  NavigationActionType,
  NavigationSubMenuItem
} from './navigation.config';
import {AdaptTranslateService} from '../common/i18n/index';
import {debounce} from '../common/utilities';
import {AdaptDropdownDirective} from '../dropdown/index';
import {ReplaySubject} from 'rxjs';
import {takeUntil} from 'rxjs/operators';
import {isFunction} from 'lodash-es';
import {KeyValueObject} from '../common/common.models';
import {RxSearchTextsModel} from '../rx/rx-search/index';
import {AdaptDeviceDetectionService} from '../common/device-detection.service';

let _testIdIndex: number = 1;

function _generateActionItemTestID(item: NavigationAction): string {
  if (!item.testID) {
    item.testID = `navigation-action-test-id-${_testIdIndex++}`;
  }

  return item.testID;
}

@Component({
  selector: 'adapt-nav-actions',
  templateUrl: './nav.actions.component.html'
})
export class AdaptNavActionsComponent implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  @Input() actions: NavigationAction[];

  @Input() kebabMode: boolean;

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @Input() kebabView: any;

  @Input() texts: AdaptSwitcherModel;

  @ViewChild('kebabDropdown') kebabDropdown: AdaptDropdownDirective;

  @ViewChild('switcherDropdown', {static: false}) switcherDropdown: AdaptDropdownDirective;

  isDesktop: boolean;

  clientWidth: number;

  filterValue: string = '';

  filteredItems: AdaptNavigationSubMenuItem[] = [];

  defaultTexts: {
    switcher: {
      filter?: RxSearchTextsModel;
      notFoundText?: string;
    };
  } = {
    switcher: {
      filter: {},
      notFoundText: ''
    }
  };

  onFilteringDebounce: Function = debounce(this.onFiltering.bind(this), 100);
  private _destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

  constructor(private el: ElementRef,
              private ts: AdaptTranslateService,
              private _adaptRadarService: AdaptRadarAngularService,
              private _deviceDetector: AdaptDeviceDetectionService) {
    this.setDefaultTexts(ts.getCurrentLanguage());
    this.isDesktop = this._deviceDetector.deviceDesktop();
  }

  ngOnInit(): void {
    this.texts = {...this.defaultTexts.switcher, ...this.texts};
  }

  ngOnChanges(changes: SimpleChanges): void {

    if (changes.texts) {
      this.texts = {...this.defaultTexts.switcher, ...this.texts};
    }
  }

  /**
   * gets clientWidth, used by navigation
   */

  ngAfterViewInit(): void {
    this.clientWidth = this.el.nativeElement.clientWidth;

    if (this.switcherDropdown) {
      this.switcherDropdown.onOpen
        .pipe(takeUntil(this._destroyed$))
        .subscribe(() => {
          this.filterValue = '';
        });
    }
  }

  ngOnDestroy(): void {
    this._destroyed$.next(true);
    this._destroyed$.complete();
  }

  setDefaultTexts(translation: KeyValueObject): void {
    this.defaultTexts.switcher = {
      notFoundText: translation['adapt.navigation.notFound'],
      filter: {
        placeholder: translation['adapt.common.search'],
        btnClearText: translation['adapt.common.clear'],
        btnSearchText: translation['adapt.common.search'],
        labelText: ''
      }
    };
  }

  @Input() getClientWidth(): number {
    return this.el.nativeElement.clientWidth;
  }

  /**
   * Check content type
   */
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  isTemplate(content: any): boolean {
    return content instanceof TemplateRef;
  }

  onFiltering(value: string, index: number): void {
    this.filterValue = value;

    this.filteredItems =
      // TODO: Refactor this if possible
      /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
      this.actions[index].switcher.allItems.items.filter(function (c: any): any {
        return c.name.toLowerCase().indexOf(value.toLowerCase()) !== -1;
      });
  }

  @Input() closeAllDropdowns(): void {
    if (this.switcherDropdown) {
      this.switcherDropdown.close();
    }

    if (this.kebabDropdown) {
      this.kebabDropdown.close();
    }
  }

  handlePopupOpenChange(isOpened: boolean, act: NavigationAction): void {
    if (isFunction(act.action)) {
      act.action({isOpened});
    }
  }

  generateActionItemTestID(item: NavigationAction): string {
    return _generateActionItemTestID(item);
  }
}

@Component({
  selector: 'adapt-nav-action-item',
  styles: [':host { display: flex; align-items: inherit}'],
  template: `
    <ng-container *ngIf="!item.hide">
      <span class="a-action__title" *ngIf="item.title">{{item.title}}</span>
      <a *ngIf="item.link"
         [attr.title]="item.tooltip ? null : item.name"
         [attr.aria-label]="item.name"
         [attr.href]="item.link"
         [attr.target]="getTarget(item)"
         [attr.class]="'a-action ' + item.className"
         [attr.data-testid]="generateActionItemTestID(item)"
         adaptTooltip="{{item.tooltip ? item.tooltip : null}}" placement="bottom">
        <span class="a-action__counter" *ngIf="item.count">{{item.count}}</span>
      </a>

      <a *ngIf="item.route"
         [attr.title]="item.tooltip ? null : item.name"
         [attr.aria-label]="item.name"
         routerLink="{{item.route}}"
         routerLinkActive="active"
         [attr.class]="'a-action ' + item.className"
         [attr.data-testid]="generateActionItemTestID(item)"
         adaptTooltip="{{item.tooltip ? item.tooltip : null}}" placement="bottom">
        <span class="a-action__counter" *ngIf="item.count">{{item.count}}</span>
      </a>

      <button *ngIf="item.action"
              type="button"
              (click)="item.action($event)"
              [attr.aria-label]="item.name"
              [attr.title]="item.tooltip ? null : item.name"
              [attr.class]="'a-action btn btn-link ' + item.className"
              [attr.data-testid]="generateActionItemTestID(item)"
              adaptTooltip="{{item.tooltip ? item.tooltip : null}}" placement="bottom">
        <span class="a-action__counter" *ngIf="item.count">{{item.count}}</span>
      </button>
    </ng-container>`
})
export class AdaptNavActionsItemComponent {
  @Input() item: NavigationAction;

  getTarget(item: NavigationAction): string {
    return item.target ? item.target : '_self';
  }

  generateActionItemTestID(item: NavigationAction): string {
    return _generateActionItemTestID(item);
  }
}

@Component({
  selector: 'adapt-nav-action-subitem',
  styles: [':host { display: flex; align-items: inherit}'],
  template: `
    <ng-container *ngIf="!item.hide">
      <a *ngIf="item.link"
         [attr.href]="item.link"
         [attr.class]="'a-dropdown__link dropdown-item ' + item.className"
         [attr.data-testid]="generateActionSubItemTestID(item)"
         [attr.target]="getTarget(item)"
         (click)="handleLinkClick()">{{item.name}}
        <span class="a-action__counter kebab-counter" *ngIf="item.count">{{item.count}}</span>
      </a>

      <a *ngIf="item.route"
         #routerLinkRef="routerLinkActive"
         [attr.title]="item.name"
         [attr.class]="'a-dropdown__link dropdown-item ' + item.className"
         [attr.aria-current]="routerLinkRef.isActive ? 'page' : undefined"
         routerLink="{{item.route}}"
         [attr.data-testid]="generateActionSubItemTestID(item)"
         (click)="handleRouteClick()"
         routerLinkActive="active">
        {{item.name}}
        <span class="a-action__counter kebab-counter" *ngIf="item.count">{{item.count}}</span>
      </a>

      <button *ngIf="item.action"
              type="button"
              (click)="handleActionClick($event)"
              [ngClass]="cssClass"
              [attr.data-testid]="generateActionSubItemTestID(item)"
              [attr.title]="item.name">{{item.name}}
        <span class="a-action__counter kebab-counter" *ngIf="item.count">{{item.count}}</span>
      </button>
    </ng-container>`
})
export class AdaptNavActionsSubitemComponent implements AfterContentInit {
  @Input() item: NavigationSubMenuItem;

  @Input() customClass: string;

  @Input() actionType: NavigationActionType;

  @Output() readonly emitCloseDropdowns: EventEmitter<string> = new EventEmitter<string>();

  cssClass: string = 'a-dropdown__link dropdown-item btn-link keyboard-focusable';

  constructor(private _adaptRadarService: AdaptRadarAngularService) {
  }

  generateActionSubItemTestID(item: NavigationSubMenuItem): string {
    if (!item.testID) {
      item.testID = `navigation-action-subitem-test-id-${_testIdIndex++}`;
    }

    return item.testID;
  }

  ngAfterContentInit(): void {

    // for back compatibility
    if (this.item.icon) {
      this.cssClass += ' ' + this.item.icon;
    }

    // regular class name passing by client (public API)
    if (this.item.className) {
      this.cssClass += ' ' + this.item.className;
    }

    // item might be already selected
    if (this.item.active) {
      this.cssClass += ' active';
    }

    // internal way to set class
    if (this.customClass) {
      this.cssClass += ' ' + this.customClass;
    }
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  getTarget(item: any): string {
    return item.target ? item.target : '_self';
  }

  handleLinkClick(): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationAction, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ActionType: this.actionType,
      Name: this.item.name,
      Type: 'link'
    });
  }

  handleRouteClick(): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationAction, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ActionType: this.actionType,
      Name: this.item.name,
      Type: 'route'
    });

    this.closeDropdowns();
  }

  handleActionClick(event: Event): void {
    this._adaptRadarService.sendCustomEventForAllProviders(AdaptRadarAngularCustomEventName.AdaptNavigationAction, {
      EventType: AdaptRadarAngularCustomEventType.Click,
      ActionType: this.actionType,
      Name: this.item.name,
      Type: 'action'
    });

    this.closeDropdowns();
    this.item.action(event);
  }

  closeDropdowns(): void {
    this.emitCloseDropdowns.emit();
  }
}
